import React, { Component } from 'react'
import MuiThemeProvider from 'material-ui/styles/MuiThemeProvider';
import AppBar from 'material-ui/AppBar';
import TextField from 'material-ui/TextField';
import RaisedButton from 'material-ui/RaisedButton';

class UserPersonalDetails extends Component {

    continue = event => {
        event.preventDefault();
        this.props.nextStep();
    }

    back = event => {
        event.preventDefault();
        this.props.prevStep();
    }

    
    render(){
        const { step } = this.props;
        const { value,handelChange } = this.props 
        return (
            <MuiThemeProvider>
                <React.Fragment>
                    <AppBar title="Personal Details"/>
                    <TextField 
                        hintText="Enter your Address"
                        floatingLabelText="Address"
                        onChange={handelChange('address') }
                        defaultValue={ value.address }

                    /><br />
                    <TextField 
                        hintText="Enter your Designation"
                        floatingLabelText="Designation"
                        onChange={handelChange('designation')}
                        defaultValue={ value.designation }
                    /><br />
                    <TextField 
                        hintText="Enter your Age"
                        floatingLabelText="Age"
                        onChange={handelChange('age')}
                        defaultValue={ value.age }
                    /><br />
                    <RaisedButton 
                        label='Continue'
                        onClick={this.continue}
                        primary={true}
                        style={styles.button}
                    />
                    

                    <RaisedButton 
                        label='Back'
                        onClick={this.back}
                        style={styles.button}
                    />
                </React.Fragment>
            </MuiThemeProvider>
        )
    }
}
const styles={
    button :{
        margin: 15
    }
}
export default UserPersonalDetails
